import config from '../../config';
/**
 * vue-routerhttps://router.vuejs.org/zh-cn/
 * 1name,'.'1name'home',name: 'home.xxx'
 *      2desc vue-router
 * @type {[*]}
 */
export default [
  {
    desc: '',
    name: '',
    path: '/',
    redirect () {
      // return config.emptyRoute() || config.defaultRoute();
      return config.emptyRoute();
    }
  },
  {
    desc: '',
    name: '',
    path: '/admin',
    redirect () {
      return '/admin/login';
    }
  },
  {
    desc: '',
    path: '/admin/register',
    name: 'register',
    component (resolve) {
      require.ensure([], () => resolve(require('components/register/register.vue')), 'register');
    }
  },
  {
    desc: '',
    path: '/admin/login',
    name: 'login',
    component (resolve) {
      require.ensure([], () => resolve(require('components/login/login.vue')), 'login');
    }
  },
  {
    desc: '',
    path: '/permission',
    name: 'permission',
    component (resolve) {
      require.ensure([], () => resolve(require('components/no-permission.vue')), 'permission');
    }
  },
  {
    desc: '',
    path: '*',
    name: 'not-found',
    component (resolve) {
      require.ensure([], () => resolve(require('components/not-found.vue')), 'not-found');
    }
  }
];



