import Vue from 'vue';
import App from './App';
import ElementUI from 'element-ui'; // element-ui 
import AMap from 'vue-amap';
import 'assets/fonts/iconFont/iconfont.css';// iconfont 
import 'assets/fonts/plIcon/iconfont.css';// iconfont 
import 'assets/fonts/portalIcon/iconfont.css';// iconfont 
import router from './core/router'; // 
import store from './core/store'; // vuex
import api from './core/api'; // api
import PlComponents from './plugins/components'; // 
import Filters from './plugins/filters'; // 
import Vue2Filters from 'vue2-filters'; // vue1vue2 https://github.com/freearhey/vue2-filters
import config from './config';  // 
import './core/mixins/permission';
import './core/mixins/msg-tip';
import './assets/scss/element-variables.scss'; // element-ui 
import './assets/scss/theme-reset.scss'; // element-ui 
import './assets/scss/_index.scss'; // 

//  (api)
if (config && config.modules) {
  Object.keys(config.modules).map((item) => {
    Vue.use(config.modules[item]);
  });
}

Vue.use(ElementUI, {size: 'medium'}); // ElementUI
Vue.use(api); // api
Vue.use(PlComponents); // 
Vue.use(Filters); // 
Vue.use(Vue2Filters); // 
Vue.use(router); // 
Vue.use(AMap); // 

// vue-amap
AMap.initAMapApiLoader({
  // key
  key: '31d7a67979b35b73f9d413c5415cac9a',
  // 
  plugin: ['Autocomplete', 'PlaceSearch', 'Scale', 'OverView', 'ToolBar', 'MapType', 'PolyEditor', 'AMap.CircleEditor'],
  uiVersion: '1.0', // 
  v: '1.4.4'
});

// 
/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router: router.router.instance,
  store //  store:store
});



